@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;0,600;1,400&display=swap);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
:root {
    --color-primary: #2876be;
    --color-primary-hover: #2266a5;
    --color-secondary: #88daa7;
    --color-light: #f8f8f8;
    --color-light-grey: #d3d3d3;
    --color-grey: #8b8b8b;
    --color-dark: #2e2e2e;
}

*,
::before,
::after {
    box-sizing: border-box;
}

body {
    margin: 0;
    font-family: 'Poppins', sans-serif;
}

h1 {
    margin: 0;
    font-size: 1rem;
    font-weight: 400;
}

button {
    border: none;
    background-color: transparent;
    cursor: pointer;
    font-family: inherit;
    font-size: inherit;
}

.home {
    height: 100%;
    background-color: var(--color-light);
}

.wrapper {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.users-loading-container {
    display: flex;
    justify-content: center;
    margin-top: 1rem;
}

.users-loading {
    border-radius: 1rem;
    padding: 1rem;
    background-color: var(--color-light-grey);
}

.user-tiles-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: 90%;
    padding: 0.5rem;
}

.pagination-container {
    position: absolute;
    bottom: 0.5rem;
    display: flex;
    justify-content: center;
    width: 100%;
}

.pagination-controllers {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    border-radius: 0.5rem;
    background-color: var(--color-light-grey);
}

.pagination-current-page-number {
    display: flex;
    justify-content: center;
    padding: 0 1rem;
    width: 2.5rem;
}

.pagination-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 0.5rem;
    padding: 0.3rem;
    color: var(--color-light);
    background-color: var(--color-primary);
}

.pagination-btn:hover {
    background-color: var(--color-primary-hover);
}

.pagination-btn-before {
    left: 1rem;
}

.pagination-btn-next {
    right: 1rem;
}

@media (min-width: 720px) {
    .pagination-container {
        bottom: 2rem;
    }
}

.user-tile {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin-top: 1rem;
    padding: 1rem;
    border-radius: 1rem;
    font-size: 0.8rem;
    color: var(--color-dark);
    background-color: var(--color-light-grey);
    text-decoration: none;
    -webkit-animation: tile-in 0.4s;
            animation: tile-in 0.4s;
    transition: background-color 0.2s;
}

@-webkit-keyframes tile-in {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes tile-in {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.user-tile:hover,
.user-tile:focus {
    background-color: var(--color-grey);
}

.user-img-container {
    display: flex;
    width: 4rem;
    height: 4rem;
    border-radius: 10rem;
    overflow: hidden;
    border: 0.3rem solid var(--color-secondary);
}

.user-img {
    object-fit: cover;
}

.user-text-info-container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    padding: 1rem;
    border-radius: 1rem;
    background-color: var(--color-light);
}

.user-text-info-item-container {
    display: flex;
    align-items: center;
}

.user-text-info-icon {
    font-size: 1rem;
}

.user-text-info-item {
    margin: 0;
    margin-left: 0.5rem;
}

.user-details-btn {
    margin-top: 1rem;
    padding: 0.3rem 0.5rem;
    border-radius: 0.7rem;
    background-color: var(--color-secondary);
}

@media (min-width: 720px) {
    .user-tile {
        width: 45%;
        margin: 1% 1%;
    }
}

@media (min-width: 1024px) {
    .user-tile {
        width: 23%;
        margin: 1% 1%;
    }
}

.navbar {
    position: relative;
    width: 100%;
    display: flex;
    justify-content: center;
    padding: 0.8rem;
    background-color: var(--color-primary);
    color: var(--color-light);
}

.navbar-icon {
    position: absolute;
    left: 1rem;
    color: var(--color-light);
    text-decoration: none;
    cursor: pointer;
}

.App {
    display: flex;
    flex-direction: column;
    height: 100vh;
}

.go-back-btn {
    display: flex;
    align-items: center;
    margin-top: 1.5rem;
    padding: 0.3rem 1rem 0.3rem 0.5rem;
    border-radius: 1rem;
    color: var(--color-light);
    background-color: var(--color-primary);
    text-decoration: none;
}

.go-back-btn:hover,
.go-back-btn:focus {
    background-color: var(--color-primary-hover);
}

.user-details-text-info-item-container {
    display: flex;
    margin: 0.4rem 0;
}

.user-details-text-info-item-icon {
    font-size: 1rem;
}
.user-details-text-info-item-text {
    margin-left: 0.5rem;
}

.user-details {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    height: 100%;
    padding: 0 1.5rem 1.5rem;
    background-color: var(--color-light-grey);
    font-size: 0.8rem;
    -webkit-animation: details-in 0.4s;
            animation: details-in 0.4s;
}

@-webkit-keyframes details-in {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes details-in {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.user-details-tile {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
}

.user-details-picture-container {
    position: relative;
    top: 2rem;
    width: 126px;
    height: 126px;
    overflow: hidden;
    border-radius: 10rem;
    border: 0.3rem solid var(--color-secondary);
}

.user-details-text-info-container {
    padding: 2.5rem 1.5rem 1rem;
    border-radius: 1rem;
    background-color: var(--color-light);
    width: 100%;
}

.not-found-container {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
    font-size: 0.8rem;
}

.not-found {
    margin: 2rem 0 0;
    padding: 1rem;
    border-radius: 1rem;
    background-color: var(--color-light-grey);
    text-align: center;
}

.not-found p {
    margin: 0;
}

